<template>
  <div id="slider-box">
    <slider :pages="pages" :sliderinit="sliderinit">
      <!-- slot  -->
    </slider>
  </div>
</template>
<script>
  import slider from 'vue-concise-slider'// 引入slider组件
  export default {
    name:'NavBanner',
    data () {
    return {
      //图片列表[arr]
      pages:[
        {
          title: '',
          style:{
            background:'url(../../static/img/banner.jpg)'
          }
        },
        {
          title: '',
          style:{
            background:'url(../../static/img/banner1.jpg)'
          }
        },
        {
          title: '',
          style:{
            background:'url(../../static/img/banner3.jpg)',
          },
        }
      ],
      //滑动配置[obj]
      sliderinit: {
        currentPage: 0,//当前页码
        thresholdDistance:100,//滑动判定距离
        thresholdTime: 500,//滑动判定时间
        autoplay:1500,//自动滚动[ms]
        loop:true,//循环滚动
        direction:'horizontal',//方向设置，垂直滚动
        infinite:1,//无限滚动前后遍历数
        slidesToScroll:1,//每次滑动项数
      }
    }
  },
  components: {
    slider
  }
}
</script>
<style scoped>
  #slider-box,.slider-container,.slider-item{
    width: 100%;
    height: 100%;
  }
  .slider-container{
    margin: 0!important;
  }
</style>



