<template>
  <div class="login-outbox">
    <div class="login-box col-xs-10" v-show="this.loginshow">
      <div class="login-title">用户登录</div>
      <div class="login-content">
        <input type="tel" placeholder="请输入手机号" v-model="name"/>
        <input type="password" placeholder="请输入密码" v-model="pwd"/>
      </div>
      <div class="login-btn" @click="isLogin">登录</div>
    </div>
  </div>
</template>
<script>
import jsonp from 'jsonp'
export default{
  name:'login',
  data(){
    return{
      name:'',
      pwd:'',
      loginshow:true
    }
  },
  methods:{
    isLogin:function(){
      var that=this;
      console.log(1);
      jsonp("http://101.201.146.79:8088/dts/user/getUserByNames?isGuest=0&account="+this.name+"&pwd="+this.pwd, {param:'callbackparam'}, function (err, data){
        if (err) {
          console.error(err.message);
        } else {
          if(data.desc){
            alert(data.desc);
          }else{
            window.localStorage.setItem('username',that.name);
            window.localStorage.setItem('userpass',that.pwd);
            that.$router.push('/home');
          }
        }
      });
    }
  }
}
</script>
<style scoped lang="less">
  .login-outbox{
    width: 100%;
    height:100%;
    background: url("../../static/img/banner.jpg") no-repeat;
    background-size: 100% 100%;
  }
  .login-box{
    @w:300px;
    @h:50px;
    position: absolute;
    margin: auto;
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    width:@w;
    height:@w;
    background: rgba(0,0,0,.7);
    overflow: hidden;
    .login-title{
      font-size: 30px;
      text-align: center;
      color: #fff;
      margin: 20px 0;
    }
    .login-content input{
      display: inline-block;
      width: 90%;
      height:@h;
      text-align: center;
      margin-left: 15px;
      margin-bottom: 20px;
      border: 0;
    }
    .login-btn{
      width: 90%;
      height:@h;
      border-radius:5px;
      background: #1BBC9B;
      font-size: 20px;
      color: #fff;
      text-align: center;
      line-height:@h;
      margin: 0 auto;
      cursor: pointer;
    }
  }

</style>



