import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './mutations-types'
import axios from 'axios'
Vue.use(Vuex)
const store = new Vuex.Store({
  state:{
    //头部
    curindex:0,
    nav:[
      {title:'home',text:'HOME'},
      {title:'about',text:'ABOUT'},
      {title:'work',text:'WORK'},
      {title:'contact',text:'CONTACT'}],
    //联系我
    details:[
      {name:'EMAIL',value:'626712309@qq.com'},
      {name:'PHONE',value:'18235185547'},
      {name:'QQ',value:'626712309'}
    ],
    skills:[],
    //登录
    username:window.localStorage.getItem('username'),
    userpass:window.localStorage.getItem('userpass'),
    //音乐播放器
    index: 0,
    //所有的音乐列表
    musics:'',
    //单独的一个
    music:{
      "img":"https://y.gtimg.cn/music/photo_new/T002R500x500M000001pmrh74IRUZR.jpg",
      "name":"一个人",
      "src":"http://100.100.100.100:8081/IXC846edcafe0153bab49aadb857335e786/C400003Kf2wo2cowwV.m4a?vkey=B74BA5D80AB2BB329D156201F2C9AD4A7BCE8A706F809E885D5F0C94CC636C789A69D4A72338591F00BBC41BFCCAD079C39DE7A39ED028D9&guid=7865329264&uin=0&fromtag=66"
    },
    playing: false
  },
  getters:{
    [types.DONE_SKILL_DATA]:state=>{
      return state.skills
    },
    [types.DONE_MUSIC_DATA]:state=>{
      return state.musics
    }
  },
  mutations:{
    [types.MAKE_CUR](state,index){
      state.curindex=index
    },
    [types.TOGGLE_SKILL_DATA](state,all){
      state.skills.push(all)
    },
    [types.TOGGLE_MUSIC_DATA](state,all){
      state.musics=all
    },
    //正在播放的
    playIndex (state, index) {
      state.index = index;
      state.music = state.musics[index]
    },
    play (state) {
      state.playing = true
    },
    pause (state) {
      state.playing = false
    },
    playFront (state) {
      state.playing = true;
      state.index = (state.index - 1 + state.musics.length) % state.musics.length
      state.music = state.musics[state.index]
    },
    playNext (state) {
      state.playing = true;
      state.index = (state.index + 1) % state.musics.length
      state.music = state.musics[state.index]
    },
  },
  actions:{
    makeCur({commit},index){
      commit(types.MAKE_CUR,index)
    },

    [types.FEACH_SKILL_DATA]({commit}){
      axios.get("../../static/skillDetail.json")
        .then(response => {
          commit(types.TOGGLE_SKILL_DATA,response.data.skills);
          console.log(response.data);
        }).catch(err => console.log(err))
    },
    [types.FEACH_MUSIC_DATA]({commit}){
      axios.get("../../static/music.json")
        .then(response => {
          commit(types.TOGGLE_MUSIC_DATA,response.data.musics);
        }).catch(err => console.log(err))
    }
  }
})
export default store



