<template>
  <div class="contain-box container-fluid">
    <div class="top-box">
      <div class="top-nav col-sm-6 col-md-6">
        <div class="logo-box"><img src="../assets/logo.png" alt=""/></div>
      </div>
      <!--手机屏幕的时候-->
      <button class="navbar-toggle collapsed" @click="togglenav">
        <span class="sr-only">Toggle navigation</span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
      </button>
      <ul class="nav-box col-xs-12 col-sm-6 col-md-6" v-show="navstate">
        <li class="nav-list col-sm-3 col-xs-12 col-md-3"  v-for="(navlist,index) in nav" :class="curindex==index?'cur':''" @click="makeCur(index)">
          <router-link :to="navlist.title">{{navlist.text}}</router-link>
        </li>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
  export default{
    name: 'HeadNav',
    computed:{
      curindex: function () {
        return this.$store.state.curindex
      },
      nav:function(){
        return this.$store.state.nav
      }
    },
    data(){
      return{
        navstate:true,
        screenWidth:document.body.clientWidth
      }
    },
    mounted(){
      //没改变浏览器大小的时候
      this.navstate = this.screenWidth <= 768 ? false:true
      const that = this
      window.onresize = () => {
        return (() => {
          window.screenWidth = document.body.clientWidth
          that.screenWidth = window.screenWidth
        })()
      }
    },
    watch: {
      screenWidth (val) {
        console.log(val)
        this.screenWidth = val
        this.navstate = this.screenWidth <= 768 ? false:true
      }
    },
    methods:{
      makeCur(index){
         this.navstate = this.screenWidth <= 768 ? false:true
         this.$store.dispatch('makeCur',index);
      },
      togglenav(){
        this.navstate=this.navstate ? false : true
      }
    }
  }
</script>
<style lang="less">
  /*媒体查询*/
  @screen-xs-max:768px;
  @media (max-width:@screen-xs-max) {
    .nav-box{
      background: #1BBC9B;
      margin-top: 0!important;
    }
  }
  .container-fluid{
    padding: 0!important;
  }
  .icon-bar{
    background: #333333;
  }
  .navbar-toggle{
    margin-top: 20px;
  }
  .top-box{
    /*display: none;*/
    background: #fff;
    position: fixed;
    top:0;
    width: 100%;
    margin: 0 auto;
    height: 80px;
    /*overflow: hidden;*/
    z-index: 1000;
    .logo-box{
      float: left;
      img{
        margin-top: 11px;
      }
    }
  }
  .top-box .nav-box{
    float: right;
    margin-top:25px;
    overflow: hidden;
    .nav-list{
      @w:70px;
      @h:30px;
      float: left;
      text-align: center;
      line-height: 30px;
      transition: all 0.9s ease;
      &.cur{
        background: #1BBC9B;
        color: #fff;
        a{
          color: #fff;
        }
      }
    }
  }
</style>



