<template>
  <div class="person-data col-xs-12 col-sm-6">
    <div class="about-title">
      About Me
    </div>
    <div class="person-my">
      <div class="my-img col-sm-3">
        <img src="../assets/me.png" alt=""/>
      </div>
      <canvas ref="canvas" id="myCanvas" class="col-xs-8 col-sm-9" height="400"></canvas>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        data:['姓名：李灏峰','求职意向：前端工程师','年龄：二十四','性格：积极乐观','爱好：读书音乐','做事风格：沉重稳重','激励名言：继续前进永不止步']
      }
    },
    mounted:function(){
      this.init()
    },
    methods:{
      init(){
        let canvas=this.$refs.canvas;
        let context=canvas.getContext('2d');
        context.fillStyle='rgba(0,0,0,0)';
        context.fillRect(0,0,canvas.width,canvas.height);
        //折现
        let points = [];
        points.push({"x":6,"y":50},{"x":50,"y":100},{"x":6,"y":150},{"x":50,"y":200},{"x":6,"y":250},{"x":50,"y":300},{"x":6,"y":350});
        context.beginPath();
        context.moveTo(points[0].x, points[0].y);
        for(let i= 1; i< points.length; i++){
          context.lineTo(points[i].x,points[i].y);
        }
        context.lineWidth = 3;
        context.strokeStyle = "#1BBC9B";
        context.stroke();
        context.closePath();
        //绘制坐标图形
        for(let i in points){
          let p = points[i];
          context.beginPath();
          context.arc(p.x,p.y,4,0,2*Math.PI);
          //空心圆
          context.strokeStyle = "#1BBC9B";
          context.stroke();
          context.fillStyle="white";
          context.fill();
        }
        //添加文字
        for(let i in points) {
          let p = points[i];
          context.beginPath();
          context.fillStyle="#fff";
          context.font="14px Calibri";
          context.fillText(this.data[i], p.x+15, p.y+5);
        }
      }
    }
  }
</script>
<style>
  .person-data{
   /* width: 50%;
    float: left;*/
  }
  .about-title{
    margin-top: 100px;
    color: #fff;
    text-align: center;
    height: 30px;
    line-height: 30px;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #1BBC9B;
    text-shadow: 0 0 3px #333;
    font-size: 20px;
  }
  .my-img,canvas{
    float: left;
  }
  .my-img{
    width: 90px;
    height: 90px;
  }
  .my-img img{
    text-align: center;
    width: 100%;
    border-radius: 50%;
  }
  .person-my{
    width: 470px;
    margin: 0 auto;
  }
</style>



