<template>
    <div class="play-bar" id="play-bar">
      <audio id="music" :src="music.src" @ended="ended"></audio>
      <div class="play-bar-img">
        <img :src="music.img" alt=""/>
      </div>
      <p class="play-bar-text">{{music.name}}</p>
      <ul class="play-bar-button">
        <li class="play-list-btn" @click.stop.prevent="prevButton">
          <img src="../assets/prev.png" alt=""/>
        </li>
        <li class="play-list-btn" @click.stop.prevent="tapButton">
          <img :src="playing?'../../static/img/play.png':'../../static/img/pause.png'" alt=""/>
        </li>
        <li class="play-list-btn" @click.stop.prevent="nextButton">
          <img src="../assets/next.png" alt=""/>
        </li>
      </ul>
    </div>
</template>
<script>
  import {mapGetters,mapState} from 'vuex'
  export default{
    name:'music',
    data(){
       return{

       }
    },
    computed:{
      ...mapGetters({
        musics:'DONE_MUSIC_DATA'
      }),
      ...mapState({
          curindex: state => state.index,
          playing: state => state.playing,
          music: state => state.music
      })
    },
    created(){
      this.$store.dispatch('FEACH_MUSIC_DATA');
    },
    mounted(){},
    methods:{
      tapButton(){
        this.playing ? this.$store.commit('pause') : this.$store.commit('play');
      },
      prevButton(){
        this.$store.commit('playFront');
        document.getElementById('music').setAttribute('autoplay',true)
      },
      nextButton(){
        this.$store.commit('playNext');
        document.getElementById('music').setAttribute('autoplay',true)
      },
      ended(){
        this.$store.commit('playNext');
        document.getElementById('music').setAttribute('autoplay',true)
      }
    },
    watch:{
      playing(val){
        if (val) {
          console.log('播放');
          document.getElementById('music').play()
        } else {
          console.log('暂停');
          document.getElementById('music').pause()
        }
      }
    }
  }
</script>
<style scoped lang="less">
  .play-bar{
    @w:320px;
    @h:38px;
    display: flex;
    position: fixed;
    bottom:0;
    width:@w;
    height:50px;
    padding: 6px 0;
    background: rgba(255,255,255,0.7);
    background: #fff;
    right: 0;
    z-index: 1000;
    .play-bar-img{
      padding-left: 15px;
      cursor: pointer;
      img{
        width:@h;
        height:@h;
        border-radius: 5px;
      }
    }
    .play-bar-text{
      line-height:@h;
      margin-left: 10px;
      font-size: 16px;
      flex: 1;
    }
    .play-bar-button{
      display: flex;
      flex: 2;
      li{
        @w:20px;
        @h:20px;
        width:@w;
        height:@h;
        padding-right: 15px;
        cursor: pointer;
      }
    }
    .play-list-btn{
      flex: 1;
      img{
        height:@h;
      }
    }
  }
</style>



