<template>
<div class="work-box">
  <div class="work-title">
    Work Display
  </div>
  <ul class="work-detail">
    <li class="work-list col-xs-6 col-sm-3 col-md-3" v-for="(wlist,index) in worklists" @mouseenter="enter(index)" @mouseleave="leave(index)">
      <img v-lazy="wlist.img" alt="" ref="wimg"/>
      <div class="mask-box" ref="mask">
        <p class="w-name">{{wlist.name}}</p>
        <p class="w-link">
          <a class="fa fa-link" :href="wlist.url">
            <img src="../assets/link.jpg" alt=""/>
          </a>
        </p>
      </div>
    </li>
  </ul>
</div>
</template>
<script>
import axios from 'axios'
export default{
  name:'WorkDetail',
  data(){
    return{
      worklists:[]
    }
  },
  mounted(){
    axios.get("../../static/workDetail.json")
      .then(response => {
      this.worklists= response.data.works;
    })
  },
  methods:{
    enter(index){
      this.$refs.wimg[index].style='transform: scale3d(1.2, 1.2, 1.2) rotate(10deg);';
      this.$refs.mask[index].style='z-index:3';
    },
    leave(index){
      this.$refs.wimg[index].style='transform: scale3d(1, 1, 1) rotate(0deg);';
      this.$refs.mask[index].style='z-index:1';
    }
  }
}
</script>
<style lang="less">
  @media (max-width: 768px) {
    .work-detail .work-list{
      height: 150px!important;
    }
    .mask-box p.w-name{
      margin: 25px auto!important;
      font-size: 16px!important;
    }
    .mask-box p.w-link, .mask-box .w-link a,.mask-box p.w-link img, .mask-box .w-link a img{
      width: 40px!important;
      height: 40px!important;
    }
  }
  .col-sm-3,.col-md-3{
    padding:0!important;
  }
  .pos(@loc:absolute){
    position:@loc;
  }
  .work-box{
    overflow: hidden;
    width: 100%;
    min-height: 100%;
    background: url("../assets/workbg.jpg") no-repeat;
    background-size: cover;
  }
  .work-title{
    @h:30px;
    margin-top: 100px;
    color: #333;
    text-align: center;
    height:@h;
    line-height:@h;
    font-size:2em;
    margin-bottom:20px;
  }
  .work-detail{
    width: 90%;
    margin: 0 auto;
    overflow: hidden;
    padding-bottom:80px;
    .work-list{
      .pos(relative);
      /*width: 25%;*/
      height: 230px;
      overflow: hidden;
      float: left;
      img{
        transition: all 2s ease;
        .pos();
        width: 100%;
        z-index: 2;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .mask-box{
    .pos();
    @w:50px;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    overflow: hidden;
    p.w-name{
      margin:@w auto;
      font-size: 20px;
      color: #fff;
      text-align: center;
    }
    p.w-link,.w-link a{
      display: inline-block;
      width:@w;
      height:@w;
      border-radius: 50%;
      background: #F1F1F1;
      margin: 0 auto;
      cursor: pointer;
      overflow: hidden;
      .pos();
      left: 0;right: 0;
      img{
        text-align: center;
        width:@w;
        height:@w;
        vertical-align: top;
      }
    }
  }

</style>



