<template>
  <div class="skill-detail col-xs-12 col-sm-6">
    <div class="about-title">
      Skill Detail
    </div>
    <ul class="skill-box">
      <li class="skill-list col-xs-6 col-sm-3" v-for="(list,index) in lists" @click="makeCur(index)" :class="skillindex==index?'cur':''">{{list.name}}</li>
    </ul>
    <ul class="skill-content">
      <li class="sk-con-list" v-for="listcon in listcons">
        <span class="sk-con col-xs-6 col-sm-3" v-for="skbox in listcon.detail">{{skbox}}</span>
      </li>
    </ul>
  </div>
</template>
<script>
import {mapGetters} from 'vuex'
import axios from 'axios'
export default{
  name:'SkillDetail',
  data(){
    return{
      lists:[],
      listcons:[],
      skillindex:0
    }
  },
  mounted(){
    axios.get("../../static/skillDetail.json")
      .then(response => {
      this.lists = response.data.skills;
      this.listcons.push(this.lists[0]);
    })
  },
  methods:{
    makeCur(index){
      this.skillindex=index;
      this.listcons.splice(0,1,this.lists[index]);
      return this.listcons;
    }
  }
}
</script>
<style lang="less">
@media (max-width: 768px) {
  .skill-detail .about-title{
    margin-top: 0!important;
  }
}
.skill-detail{
  /*float: left;
  width: 50%;*/
}
.skill-box{
  /*width: 380px;*/
  overflow: hidden;
  margin: 0 auto;
}
.skill-list{
  height: 34px;
  /*border-radius: 5px;*/
  color: #ffffff;
  background: #1BBC9B;
  text-align: center;
  /*width: 80px;*/
  float: left;
  line-height: 34px;
  cursor: pointer;
  /*margin-right: 20px;*/
  margin-top: 10px;
    &.cur{
      color: rgb(18, 116, 95);
    }
    &:nth-last-child(1){
      margin-right: 0;
    }
}
.sk-con-list{
  overflow: hidden;
  padding-bottom: 80px;
}
.sk-con{
  @w:150px;
  @h:50px;
  /*width:@w;*/
  height:@h;
  color: #f1f1f1;
  font-size: 19px;
  text-align: center;
  line-height:@h;
  float: left;
  /*margin-left:20px;*/
  margin-top: 25px;
}
</style>



